
// fqa 頁面
.faq {
  background-image: url(../images/faq.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;

  .qa {
    display: flex;
    align-items: center;
    padding-top: 214px;
    margin-left: 360px;
  }

  h2 {
    font-size: 64px;
    line-height: 90px;
    color: $blackColor;
    font-weight: 900;
  }
}


.faq-list {
background-color: $secondaryColor;
padding-left: 360px;
padding-right: 360px;
margin-bottom: 156px;
}

.faq-title {
text-align: left;
font-size: 32px;
line-height: 45px;
padding-top: 88px;
margin-bottom: 16px;
font-weight: 900;
}

.faq-items {
  border: 1px solid $blackColor;
}

.faq-item-q {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  line-height: 23px;
  color: $blackColor;
  text-align-last: left;
  padding-top: 22px;
  padding-bottom: 22px;
  padding-left: 38px;
  font-weight: 900;
  border-bottom:1px solid $blackColor;
}

.collapse ,.deliver {
 margin-right: 40px;
}


.faq-item-ans {
    font-size: 16px;
    line-height: 24px;
    color: $blackColor;
    background-color: #F2F2F2 ;
    text-align-last: left;
    padding-top: 22px;
    padding-bottom: 22px;
    padding-left: 38px;
    font-weight: 400;
    border-bottom:1px solid $blackColor;
    border-left: 1px solid $blackColor;
}

.faq-items li:last-child {
    border: none;
}