// index 首頁

//01 早春系列
.header {
  background-image: url(../images/68.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  .header-title {
    align-items: center;
    padding: 420px 0 420px 0;
  }

  h2 {
    font-size: 36px;
    line-height: 54px;
    color: $blackColor;
    font-weight: 500;
    text-align: left;
    margin-left: 1034px;
    color: $primaryColor;
  }

  h1 {
    font-size: 64px;
    line-height: 96px;
    color: $blackColor;
    font-weight: 900;
    text-align: left;
    margin-left: 984px;
    color: $primaryColor;
  }
}

// 用專業的心
.service {
  background-color: #fbf2f2;
  .service-container {
    // padding: 272px 0 272px 0;
    margin: 0;
  }

  .service-title {
    display: flex;
    justify-content: center;
    padding-top: 64px;
    h2 {
      font-size: 48px;
      line-height: 72px;
      color: $blackColor;
      font-weight: 900;
      color: $thiredColor;
    }
  }
}

/*-----*/
.service-item {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 60px;
  padding-bottom: 104px;
}

.item {
  width: 282px; /*??*/
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;

  h3 {
    font-size: 24px;
    color: $thiredColor;
    font-weight: 900;
    margin-bottom: 4px;
  }

  p {
    font-size: 16px;
    text-align: left;
    color: #000000;
  }
}

.txtbox {
  margin-right: 37px;
  margin-left: 37px;
}

.item-gap {
  margin-right: 12px;
  margin-left: 12px;
}


// 經典系列鏡框
.frames-item {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 60px;
  padding-bottom: 80px;

  h3 {
    font-size: 48px;
    line-height: 72px;
    font-weight: 900;
    margin-bottom: 4px;
    color: $thiredColor;
  }
}

.frames {
  background-color: #ffffff;
  padding-bottom: 48px;
  .frames-container {
    margin: 0;
  }

  .frames-title {
    display: flex;
    justify-content: center;
    padding-top: 64px;
    margin-bottom: 64px;
    h3 {
      font-size: 48px;
      line-height: 72px;
      color: $blackColor;
      font-weight: 900;
      color: $thiredColor;
    }
  }
}

.frame {
  display: flex;
  margin-left: 360px;
  margin-bottom: 32px;
}

.frame_fdc {
  flex-direction: row-reverse;
  justify-content: flex-end;
}

.modle {
  display: block;
  margin-right: 24px;
}

.modleShowGray {
  filter: grayscale(100%);
}
.modleShowGray:hover {
  filter: grayscale(0%);
}

.product {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.productTxt {
  padding-left: 8px;
  color: #aa0601;
  font-size: 48px;
  text-transform: uppercase;
  font-weight: 900;
}

.productHide {
  display: flex;
  align-items: center;
  margin: 0 auto;
  justify-content: center;
  width: 352px;
  height: 134px;
  background-size: contain;
  background-position: left center;
  background-repeat: no-repeat;
  display: none;
  p {
    color: #ffffff;
    font-size: 28px;
  }
}


// 聯絡我們
.contact{
  background-color:#FBF2F2;
}
.contact-title {
  margin: 0 auto;
  max-width: 792px;
  text-align: left;
  padding-bottom: 92px;
  p {
    font-size: 20px;
    color: 000000;
    padding-top: 24px;
    margin-bottom: 20px;
  }
}
.info {
  font-size: 48px;
  padding-top: 64px;
  text-align: center;
}

input[type='text'],
textarea {
  width: 100%;
  margin-top: 4px;
  margin-bottom: 16px;
  padding: 14px 16px;
  border: 1px solid #000;
  background-color: transparent;
}

textarea {
  resize: none;
  height: 136px;
}

.submit {
  margin-top: 42px;
  text-align: center;
}

input[type='submit'] {
  padding: 16px 58px;
  cursor: pointer;
  border: none;
  background: #aa0601;
  color: #fff;
}

input[type='checkbox'] + label {
  display: block;
  cursor: pointer;
  padding-left: 4px;
}

input[type='checkbox'] {
  display: none;
}

input[type='checkbox'] + label::before {
  content: '\2714';
  border: 1px solid #000;
  border-radius: 10%;
  display: inline-block;
  width: 16px;
  height: 16px;
  padding: 1px 0 1px 3px;
  margin-right: 8px;
  color: transparent;
}

input[type='checkbox']:checked + label:before {
  background-color: #aa0601;
  border-color: #aa0601;
  color: #fff;
}

