
// layout 樣板
.main-container {
    background-color: $primaryColor;
  }

.menu {
    padding-left: 88px;
    padding-right: 88px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar {
    display: flex;
  }
  
  .navbar li a {
    display: block;
    font-size: 20px;
    color: $secondaryColor;
    line-height: 30px;
    padding: 16px;
    margin: 24px 16px;
  }
  
  .navbar li a:hover {
    padding-bottom: 5px;
    border-bottom: 3px solid #fff;
    border-radius: 5px;
  }
  
  .navbar li:last-child a {
    margin-right: 0;
    padding-right: 0;
  }
  
  // footer
  .upsection {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .upsection a {
    color: $secondaryColor;
  }
  
  .foot-text-menu,
  .foot-social-menu {
    display: flex;
    font-size: 16px;
    letter-spacing: 24;
  }
  
  .foot-text-menu {
    margin-left: 385px;
  }
  .foot-social-menu {
    margin-right: 385px;
  }
  
  .foot-text-menu li {
    margin: 48px 24px 0 24px;
  }
  .foot-text-menu li:first-child {
    margin-left: 0;
  }
  
  .foot-social-menu li {
    margin: 36px 16px 0 16px;
  }
  .foot-social-menu li:last-child {
    margin-right: 0;
  }
  
  .contact-info {
    margin: 32px 0 0 385px;
  }
  
  .contact-info li {
    color: $secondaryColor;
    text-align: left;
    font-size: 36px;
    line-height: 54px;
  }
  
  .contact-info img {
    width: 48px;
    margin-right: 8px;
  }
  
  .contact-info li:last-child {
    width: 1150px;
    padding-bottom: 24px;
    border-bottom: 1px solid  #FFFFFF ;
  }
  
  .dnsection{
    display: flex;
    justify-content: space-between;
    margin-left: 385px;
    padding-bottom: 28px;
  }
  
  .copyright{
    display: flex;
    color: $secondaryColor;
    align-items: center;
    margin-top: 24px;
  
    img{
      width: 80px;
      margin-right: 38px;
    }
  
    p{
      font-size: 16px;
      line-height: 24px;
      color: $secondaryColor;
    }
  }
    
  .imgSet{
    display: flex;
    text-align: center;
  }
  
  .policy{
    margin-top: 40px;
    margin-right: 385px;
    display: flex;
    justify-content: space-between;
    
    a {
      color: $secondaryColor;
    }
  }
  
  .policy li:first-child {
    margin-right: 62px;
  }
  